import React, { useState } from 'react';
import {
  Button,
  Modal,
  Input,
  Form,
  Select,
} from 'antd';
import './Modal.scss';
import editSvg from '../../icons/navigation/mode_24px.svg';
import UAIcon from '../../icons/modal/UA.svg';

const PlusIcon = () => (
  <svg width="14" height="15" viewBox="0 0 14 15" fill="none" xmlns="http://www.w3.org/2000/svg">
    <path d="M14 8.12247H8V14.1519H6V8.12247H0V6.11266H6V0.083252H8V6.11266H14V8.12247Z" fill="#2C7DFA" />
  </svg>

);

const CloseIcon = () => (
  <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
    <path d="M11.8333 1.42661L10.6583 0.24585L6 4.92702L1.34167 0.24585L0.166672 1.42661L4.82501 6.10778L0.166672 10.7889L1.34167 11.9697L6 7.28854L10.6583 11.9697L11.8333 10.7889L7.175 6.10778L11.8333 1.42661Z" fill="#ADB5BD" />
  </svg>
);

export const EditModal: React.FC = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  const { Option } = Select;

  const prefixSelector = (
    <Form.Item name="prefix" noStyle>
      <Select style={{ width: 70 }}>
        <Option value="86"><img src={UAIcon} alt="" /></Option>
        <Option value="87">+87</Option>
      </Select>
    </Form.Item>
  );

  return (
    <>
      <Button type="ghost" onClick={showModal} className="modalButton">
        <img src={editSvg} alt="" className="customDropIcon" />
        Редагувати
      </Button>
      <Modal
        open={isModalOpen}
        onOk={handleOk}
        footer={[
          <Button key="back" onClick={handleCancel}>
            Додати користувача
          </Button>,
        ]}
      >
        <div className="modalHeader">
          <span className="plusIcon">
            <PlusIcon />
          </span>
          <h2 className="modalTitle">Редагувати сутність</h2>
          <button onClick={handleCancel} type="button" className="closeIcon">
            <CloseIcon />
          </button>
        </div>
        <div className="form">
          <div className="inputBlock">
            <h3>Назва</h3>
            <Input placeholder="Статус посилки" className="modalInput" />
            <h3>Номер телефону</h3>
            <Form.Item
              name="phone"
              rules={[{ required: true, message: 'Please input your phone number!' }]}
              className="phoneModalInput"
            >
              <Input
                addonBefore={prefixSelector}
                style={{ width: '100%' }}
                placeholder="1 (999) 999-9999"
                className="phoneModalInput"
              />
            </Form.Item>
            <h3>Група користувачів</h3>
            <Select
              defaultValue="default"
              style={{ width: 120 }}
              className="modalInput"
              options={[
                {
                  value: 'default',
                  label: 'Оберіть групу',
                  disabled: true,
                },
                {
                  value: 'group1',
                  label: '1 Група',
                },
                {
                  value: 'group2',
                  label: '2 Група',
                },
                {
                  value: 'group3',
                  label: '3 Група',
                },
              ]}
            />

            <h3>Додати нове поле</h3>
            <Select
              defaultValue="default"
              style={{ width: 120 }}
              className="modalInput"
              options={[
                {
                  value: 'default',
                  label: 'Заголовок',
                  disabled: true,
                },
                {
                  value: 'group1',
                  label: 'Текст',
                },
                {
                  value: 'group2',
                  label: 'Текст',
                },
                {
                  value: 'group3',
                  label: 'Текст',
                },
              ]}
            />
          </div>

          <div className="inputBlock">
            <h3>Ім`&apos;`я та Прізвище</h3>
            <Input placeholder="Через пробіл" className="modalInput" />
            <h3>Email</h3>
            <Form.Item
              name="email"
              rules={[
                {
                  type: 'email',
                  message: 'The input is not valid E-mail!',
                },
                {
                  required: true,
                  message: 'Please input your E-mail!',
                },
              ]}
            >
              <Input placeholder="example.com" className="modalInput" />
            </Form.Item>
            <h3>Мова</h3>
            <Select
              defaultValue="default"
              style={{ width: 120 }}
              className="modalInput"
              options={[
                {
                  value: 'default',
                  label: 'Оберіть мову',
                  disabled: true,
                },
                {
                  value: 'language1',
                  label: 'Українська',
                },
                {
                  value: 'language2',
                  label: 'English',
                },
                {
                  value: 'language3',
                  label: 'Polski',
                },
              ]}
            />
            <h3>Значення поля</h3>
            <Input placeholder="Статус посилки" />
          </div>
        </div>
      </Modal>
    </>
  );
};
