import React from 'react';
import { Table, Button, Dropdown } from 'antd';
import { ColumnsType } from 'antd/es/table';
import { user } from '../../data';
import './Table.scss';
import { EditModal } from '../Modal/EditModal';

import optionSvg from '../../icons/navigation/more_vert_24px.svg';
import saveSvg from '../../icons/navigation/save_alt_24px.svg';
import deleteSvg from '../../icons/navigation/delete_24px.svg';

interface DataType {
  action: string;
  name: string;
  url: string;
  userName: string;
  data: string;
}

const columns: ColumnsType<DataType> = [
  {
    title: '',
    dataIndex: 'action',
    key: 'action',
    render: () => (
      <>
        <Dropdown overlay={(
          <ul className="customDrop">
            <li className="customDropItem">
              <EditModal />
              <hr className="divider" />
            </li>
            <li className="customDropItem">
              <Button type="ghost" className="custopDropButton">
                <img src={saveSvg} alt="" className="customDropIcon" />
                Зберегти як файл
              </Button>
              <hr className="divider" />
            </li>
            <li className="customDropItem">
              <Button type="ghost" className="custopDropButton">
                <img src={deleteSvg} alt="" className="customDropIcon" />
                Видалити
              </Button>
            </li>
          </ul>
        )}
        >
          <Button size="small" type="ghost">
            <img src={optionSvg} alt="" />
          </Button>
        </Dropdown>
      </>
    ),
  },
  {
    title: 'Назва',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'URL',
    dataIndex: 'url',
    key: 'url',
  },
  {
    title: 'Автор',
    dataIndex: 'userName',
    key: 'userName',
  },
  {
    title: 'Створено',
    dataIndex: 'data',
    key: 'data',
    sorter: (a, b) => a.data.localeCompare(b.data),
  },
];

export const TableComponent: React.FC = () => {
  return (
    <Table
      columns={columns}
      dataSource={user}
    />
  );
};
